@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
@import url(https://d1azc1qln24ryf.cloudfront.net/114779/Socicon/style-cf.css?9ukd8d);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css);
body {
  display: table;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { box-sizing: border-box; }

body {
	-webkit-scroll-snap-type: mandatory;
	    -ms-scroll-snap-type: mandatory;
	        scroll-snap-type: mandatory;
	-webkit-scroll-snap-points-y: repeat(100vh);
	    -ms-scroll-snap-points-y: repeat(100vh);
	        scroll-snap-points-y: repeat(100vh);
	-webkit-scroll-snap-type: y mandatory;
	    -ms-scroll-snap-type: y mandatory;
	        scroll-snap-type: y mandatory;
}

.about-image {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  width: 50%;
  height: 100vh;
  background-size: cover;
  background-position: 50% 20%;
  float: left;
}

.about-text {
  font-family: 'Roboto Slab', serif;
  vertical-align: middle;
  display: table-cell;
  font-size: calc(10px + 2vmin);;  
  -webkit-align-items: center;;  
          align-items: center;
}

.App
{
  display: -webkit-flex;
  display: flex;
}


.h1, .h2, .h3, h1, h2, h3 {
  margin-top: 20px;
  margin-bottom: 10px;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  line-height: 1.1;
  color: inherit;
}

p span 
{
  display: block;
}

ul  {
  list-style-type:  none;
}

section {
  height: 100vh;
	scroll-snap-align: start;
	text-align: center;
}

header  {
  padding: 10px;
  margin: -10px;
}

/* On hover, add a black background color with a little bit see-through */
.about-image:hover, .about-image:hover {
  cursor: pointer;
  box-shadow: 0 0 11px rgba(33,33,33,.2); 
}

#id1  {
  background-image: url(/static/media/1.80ba3082.jpg);
}

#id2 {
  background-image: url(/static/media/2.fd3c96ae.jpg);
  background-position: 73% 50%;
}

#id3 {
  background-image: url(/static/media/3.3b0740e0.gif);
  background-position: 73% 50%;
}

.pull-left  {
  text-align: left;
}


/* Caption text */
.caption-text {
  color: #f2f2f2;
  font-size:  16px;
  padding: 8px 15px;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}


@media screen and (max-width: 768px)  {
  .about-image {
    display: block;
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: 50% 40%;
  }

  .about-image id2 {
    background-position: 60% 40%;
  }

  .about-image id2 {
    background-position: 50% 50%;
  }

  .about-text {
    padding: 20px;
    font-size:  100%; 
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    vertical-align: middle;
  }

}

.hidden
{
  display: none;
}
